.progress {
  position: absolute;
  justify-content: space-between;
  display: flex;
  max-height: 428px;
  min-height: 310px;
}

.line-side {
  stroke-dasharray: 280;
  align-self: end;
}

.line-medium {
  stroke-dasharray: 560;
  height: 123px;
  width: 73%;
}

@media screen and (min-width: 1609px) {
  .progress {
    bottom: 347px;
    width: 75%;
  }

  .line-side {
    max-height: 242px;
  }

